<template>
    <div class="showPanel">
        <div class="loader__container" v-if="loading">
            <div class="loader"></div>
        </div>
        <div class='error-box' v-else-if="fetchError">
            {{ fetchError }}
        </div>
        <div class="show__container" v-else-if="content">
            <div v-if="content.banner" class='show__banner-box'>
                <img v-if="content.banner" :src="content.banner" class="show__banner" alt="Show's Banner" />
                <h2 class="show__title">
                    {{ content.seriesName }} {{ aliases }}
                </h2>
            </div>
            <h2 class="show__title" v-else>
                {{ content.seriesName }} {{ aliases }}
            </h2>



                <show-tabs :selected="selected" @onSelect="selectPanel" />

                <div class="tab-content">
                    <transition name="fade" mode="out-in">
                        <DetailsPanel
                            :content="content"
                            v-if="selected === 'details'"
                        />
                        <ActorsPanel
                            :actors="content.actors"
                            v-else-if="selected === 'actors'"
                        />
                        <SeasonPanel
                            :seasons="content.seasons"
                            :show-id="content.id"
                            v-else-if="selected === 'episodes'"
                        />
                        <PostersPanel
                            :posters="content.posters"
                            v-else-if="selected === 'posters'"
                        />
                    </transition>
                </div>
        </div>
    </div>
</template>

<style>
.showPanel {
    /* margin-top: 2rem; */
    color: white;
}

/* @media screen and (max-width: 575px) {
    .showPanel {
        margin-top: 1.1rem;
    }
} */

.show__container {
    padding-bottom: 3.5rem; /* To adjust for footer */
    max-width: 1350px;
}

@media screen and (min-width: 1024px) {
    .show__banner-box {
        position: relative;
        margin-bottom: 1rem;
    }

    .show__banner-box > .show__title {
        background-color: rgba(0,0,0,0.7);
        position: absolute;
        bottom: 0;
        width: 100%;
        margin-bottom: 0;
    }
}

@media screen and (max-width: 1023px) {
   .show__banner-box > .show__title {
        margin: 0.5rem 0 1rem;
    }
}


.show__banner {
    width: 100%;
}

@media screen and (max-width: 575px) {
    .show__banner {
        width: calc(100vw - 2.75rem);
        margin-left: -0.25rem;
    }
}

.show__title {
    margin: 1rem 0;
    font-size: 2rem;
}

.tab-content {
    padding: 0.5rem 0 0 0.5rem;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.4s ease;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}

@media screen and (max-width: 575px) {
    .show__title {
        font-size: 1.35rem;
    }
}
</style>

<script lang="ts">
import { ref, computed, defineComponent, onMounted } from "vue";

import DetailsPanel from "./panels/DetailsPanel.vue";
import ActorsPanel from "./panels/ActorsPanel.vue";
import SeasonPanel from "./panels/SeasonPanel.vue";
import PostersPanel from "./panels/PostersPanel.vue";
import useState from "../StateStore";
import ShowTabs from "./ShowTabs.vue";

export default defineComponent({
    name: "ShowDisplay",
    props: {
        id: {
            type: String,
            required: true,
        },
    },
    components: {
        DetailsPanel,
        ActorsPanel,
        ShowTabs,
        SeasonPanel,
        PostersPanel,
    },
    setup(props) {
        const { content, loading, setLoading, fetchShow } = useState();
        setLoading(true);

        const selected = ref("details");
        const fetchError = ref<string|null>(null);

        const selectPanel = (value: string) => (selected.value = value);

        const aliases = computed((): string => {
            if (content.value === null) return "";

            return content.value.aliases.length
                ? ` (AKA: ${content.value.aliases})`
                : "";
        });

        async function fetchContent() {
            try {
                // console.log("Fetching content");
                await fetchShow(props.id);
                fetchError.value = null;
                if (content.value !== null) document.title = process.env.VUE_APP_TITLE + " - " + content.value.seriesName;
            } catch (e) {
                console.log("Caught an error");
                fetchError.value = e.message;
            } finally {
                setLoading(false);
            }
        }

        onMounted(async () => {
            // console.log("Show display mounted");
            await fetchContent();
        });

        return {
            selected,
            selectPanel,
            content,
            aliases,
            loading,
            fetchError,
        };
    },
});
</script>
