import { createRouter, createWebHistory } from 'vue-router';
import NotFound from './components/NotFound.vue';
import Welcome from './components/Welcome';
import ErrorView from './components/ErrorView';
import ShowDisplay from "./components/ShowDisplay.vue";
import SearchResults from './components/SearchResults.vue';

const routes = [
    // pathMatch is the name of the param, e.g., going to /not/found yields
    // { params: { pathMatch: ['not', 'found'] }}
    // this is thanks to the last *, meaning repeated params and it is necessary if you
    // plan on directly navigating to the not-found route using its name
    { path: '/', name: 'home', component: Welcome, meta: {title: "Home"} },
    { path: "/show/:id", name: 'show', component: ShowDisplay, props: true },
    { path: "/search/:query", name: 'search', component: SearchResults, props: true, meta: {title: "Search Results"} },
    { path: "/error/:error", name: 'error', component: ErrorView, props: true, meta: {title: "Error"} },
    { path: '/:pathMatch(.*)*', name: 'not-found', component: NotFound, meta: {title: "Not Found"} },
]

const router = createRouter({
    history: createWebHistory(),
    routes: routes,
})

router.beforeEach((to, _, next) => {
    if (to.meta && to.meta.title) {
        document.title = process.env.VUE_APP_TITLE + " - " + to.meta.title
    }
    next()
})

// good example:
// router.resolve({
//     name: 'not-found',
//     params: { pathMatch: ['not', 'found'] },
// }).href // '/not/found'

export default router;