<template>
  <div class="tab-panel">
    <div class="posters__list">
      <span v-for="(poster, index) in topPosters" :key="index">
        <img :src="poster" @click="choosePoster(poster)" />
      </span>
    </div>
    <div v-if="chosenPoster">
        <teleport to='body'>
            <div class="posters__big" @click="choosePoster(null)">
                <img :src="chosenPoster" class="clickable" />
            </div>
        </teleport>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    posters: Array,
  },
  data() {
    return {
      chosenPoster: null,
      loadedPosters: 16,
      busy: false,
    };
  },
  computed: {
    topPosters() {
      return this.posters.slice(0, this.loadedPosters);
    },
  },
  methods: {
    choosePoster(src) {
      if (src !== null) {
        src = src.replace(/_cache\//, "");
      }
      if (this.chosenPoster !== src) {
        this.chosenPoster = src;
        if (src === null) {
            document.body.classList.remove('no-scroll');
        } else {
            document.body.classList.add('no-scroll');
        }
      }

    },
    handleScroll: function () {
      if (!this.selected || this.busy) return;
      // this.scrollPos = document.body.scrollHeight - window.innerHeight - document.body.scrollTop;
      const res = document.body.scrollHeight - window.innerHeight - window.scrollY;
    //   const res = window.document.height - window.outerHeight - window.scrollY;
      console.log(res);
      if (res <= 2) {
        if (this.posters.length <= this.loadedPosters) {
          return;
        }
        this.loadedPosters += 4;
        this.busy = true; // Tiny throttling to prevent double triggering
        // console.log("Scrolled to bottom, loaded posters = " + this.loadedPosters);
        setTimeout(() => (this.busy = false), 250);
      }
    },
  },
  watch: {
    posters() {
      // Update search value from parent.
      // console.log("Supervalue updated to " + newValue);
      this.loadedPosters = 16; // Reset that.
    },
  },
  mounted() {
    console.log("Created scroll");
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    console.log("Removed scroll");
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
<style>
.posters__list {
    margin: 0 -1rem;
}

.posters__list img {
  display: inline-block;
  margin: 0.6rem 0.7rem;
  border: 1px solid #808080;
  width: 17rem;
  cursor: pointer;
}

.posters__big {
    position: fixed;
    background-color: #ffffff88;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 6000;
}

.posters__big img {
  display: block;
  margin: 3rem auto;
  width: 94vw;
  height: calc(100vh - 6rem);
  object-fit: contain;
}

@media screen and (min-width: 768px) {
  .posters__big img {
    max-width: 800px;
  }
}
</style>

