<template>
<div class="tab-panel">
    <div class="details-info">
        <div v-if="content.firstAired">
            <i class="far fa-calendar-alt"></i>
            First Aired: {{firstAired}}
        </div>
        <div v-if="content.siteRating">
            <i class="far fa-star"></i>
            Rating: {{content.siteRating}}
        </div>
        <div>
            <i class="fas fa-hashtag"></i>
            Status: {{content.status}}
        </div>
        <div>
            <i class="fas fa-cloud-sun-rain"></i>
            Seasons: {{content.seasons.seasons}}
        </div>
        <div>
            <i class="fas fa-building"></i>
            Network: {{content.network}}
        </div>
        <div v-if="content.rating">
            <i class="fas fa-chess-rook"></i>
            Content Rating: {{ content.rating }}
        </div>
    </div>
    <p class="overview">{{content.overview}}</p>
    <h3 style='margin-bottom: 0'>Genres</h3>
    <ul class="genre-list">
        <li v-for="(item, index) in content.genre" :key="index">
            <a :href="getLink(item)" target="_blank">{{item}}</a>
        </li>
    </ul>
    <h3>Top Actors</h3>
    <ActorsList :actors="topActors" />
</div>
</template>

<style>
    ul.genre-list>li>a {
        color: #a5d1ff !important;
    }

    .overview {
        padding: 1rem 0 1rem 0.5rem;
        border-left: 1px solid white;
    }

    .details-info {
        padding: 0 0 0.5rem;
    }

    .details-info > div {
        margin: 0.5rem 0;
        font-size: 1.1rem;
    }

    .details-info > div > i {
        width: 1.5rem;
        text-align: center;
    }

    .genre-list {
        margin-top: 0.5rem;
    }

    .genre-list li {
        line-height: 1.5;
        font-size: 1.1rem;
    }
</style>

<script lang="ts">
import { computed, defineComponent } from 'vue';
import dayjs from 'dayjs';
import {ActorModel, ShowModel} from '../../models/Models';
import ActorsList from '../ActorsList.vue';

export default defineComponent({
    props: {
        content: {
            type: Object as () => ShowModel,
            required: true
        },
    },
    setup(props) {
        const firstAired = computed((): string => {
            return dayjs(props.content.firstAired).format('MMMM D, YYYY');
        });
        const topActors = computed((): Array<ActorModel> => {
            return props.content.actors.filter((actor: ActorModel) => actor.sortOrder <= 3).slice(0, 6);
        });

        const getLink = (item: string) => {
            return `https://www.imdb.com/search/title?genres=${item === "Science-Fiction" ? "Sci-Fi": item}`;
        };

        return {
            firstAired, topActors, getLink
        };
    },
    components: {
        ActorsList
    },
});
</script>
