
import axios from "axios";
import dayjs from "dayjs";
import { baseUrl } from "@/StateStore";
import { EpisodeModel } from "@/models/Models";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    seasons: Object,
    showId: Number,
  },
  data() {
    return {
      selectedSeason: 0,
      episodes: Array<EpisodeModel>(),
    };
  },
  computed: {
    seasonList() {
      const seasons = [];
      if (this.seasons) {
        for (let i = 0; i < this.seasons.seasons; i++) {
          seasons.push(i + 1);
        }
      }

      return seasons;
    },
  },
  methods: {
    bringMeSeason(n: number) {
      this.selectedSeason = n;
      this.episodes = [];

      axios.get(`${baseUrl}episodes/${this.showId}/${n}`).then((res) => {
        if (res.data.success === false) {
          alert("Sorry, error.");
          return;
        }

        this.episodes = res.data.episodes as EpisodeModel[];
      });
    },
    toDate(date: string) {
      return dayjs(date).format("MMMM D, YYYY");
    },
  },
  mounted() {
    this.selectedSeason = 1;
    this.bringMeSeason(this.selectedSeason);
  },
});
