<template>
    <section class="nav-pills">
        <span
            class="nav-link"
            :class="{ active: selected === 'details' }"
            @click="onSelect('details')"
            aria-label="Details"
        >
            <i class="fas fa-info"></i>
            <span>Details</span>
        </span>

        <span
            class="nav-link"
            :class="{ active: selected === 'actors' }"
            @click="onSelect('actors')"
            aria-label="Actors"
        >
            <i class="fas fa-user-ninja"></i>
            <span>Actors</span>
        </span>

        <span
            class="nav-link"
            :class="{ active: selected === 'episodes' }"
            @click="onSelect('episodes')"
            aria-label="Episodes"
        >
            <i class="fas fa-file-alt"></i>
            <span>Episodes</span>
        </span>

        <span
            class="nav-link"
            :class="{ active: selected === 'posters' }"
            @click="onSelect('posters')"
            aria-label="Posters"
        >
            <i class="far fa-file-image"></i>
            <span>Posters</span>
        </span>

    </section>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        selected: String,
    },
    emits: ["onSelect"],
    setup(props, { emit }) {
        function onSelect(value: string) {
            emit("onSelect", value);
        }

        return { onSelect };
    },
});
</script>

<style>
.nav-pills {
    display: flex;
    flex-direction: row;
}

.nav-pills > .nav-link {
    margin: 0;
    background-color: #375a7f;
    padding: 1rem 1.5rem;
    display: block;

    transition: background-color 0.35s;
    text-decoration: none !important;
    color: white;
    font-weight: 400;
    font-family: Lato;
    outline: none;
    cursor: pointer;
}

.nav-pills > .nav-link.active {
    background-color: skyblue;
}

/* @media screen and (max-width: 575px) {
    a.nav-link {
        padding-right: 1rem;
        padding-left: 1rem;
    }
} */

.nav-link > span {
    padding-left: 0.5rem;
}

@media screen and (max-width: 575px) {
    .nav-link > span {
        display: none;
    }
}
</style>
