
import dayjs from "dayjs";
import { SearchResultModel } from "@/models/Models";
import { useRouter } from "vue-router";
import useState from "@/StateStore";
import { defineComponent, onMounted, ref, watch } from "vue";

export default defineComponent({
    props: {
        query: {
            type: String,
            required: true,
        },
    },
    setup(props) {
        const router = useRouter();
        const { newSearch, loading } = useState();
        const results = ref<Array<SearchResultModel>>([]);
        const error = ref<string|null>(null);

        const onClick = (id: string) =>
            router.push({ name: "show", params: { id } });

        const dateFormat = (date: string): string =>
            dayjs(date).format("MMMM D, YYYY");

        const doSearch = async (value: string) => {
            try {
                results.value = await newSearch(value);
            } catch (e) {
                error.value = e.message;
            }
        }

        onMounted(() => {
            // console.log("Search results mounted");
            doSearch(props.query);
        });

        watch(
            () => props.query,
            (value) => {
                // console.log("Query changed");
                doSearch(value);
            }
        );

        return {
            onClick,
            results,
            dateFormat,
            loading,
            error
        };
    },
});
