import { ref, computed } from 'vue';
import axios from 'axios';
import { ShowModel, SearchResultModel } from './models/Models';

export const baseUrl = process.env.VUE_APP_REMOTE || "/";

const loading = ref<boolean>(false);
const content = ref<ShowModel|null>(null);
const error = ref<string|null>(null);
const suggestions = ref<Array<string>>([]);
const searchResults = ref<Array<SearchResultModel>>([]);

function resetState() {
    loading.value = false; // Content finished loading
    error.value = null;
    content.value = null;
}

function searchError(searchError: string) {
    resetState();
    error.value = searchError;
}

function updateContent(newContent: ShowModel|null) {
    resetState();
    content.value = newContent;
}

function addSuggestion(suggestion: string) {
    suggestion = decodeURIComponent(suggestion);
    if (suggestions.value.indexOf(suggestion) == -1) {
        console.log("New suggestion " + suggestion);
        suggestions.value.push(suggestion);
    }
}

async function newSearch(searchValue: string) {
    resetState();
    loading.value = true;

    const finalValue = encodeURIComponent(searchValue.trim());

    try {
        const res = await axios.get(`${baseUrl}search/${finalValue}`);
        if (res.data.error !== undefined) throw Error(res.data.error);

        addSuggestion(searchValue.trim());
        loading.value = false;
        searchResults.value = res.data;
        return searchResults.value;
    } catch (e) {
        searchError(e.message);
        throw e;
    }
}

function setLoading(isLoading: boolean) {
    loading.value = isLoading;
}

async function fetchShow(id: string) {
    const res = await axios.get(`${baseUrl}show/${id}`);

    if (res.data.error !== undefined) {
        searchError(res.data.error);
        throw Error(res.data.error);
    }

    updateContent(res.data);
}

function getSuggestions() {
    // console.log("Getting some suggestions");
    axios.get(`${baseUrl}suggestions`).then((res) => {
        if (res.status === 200) {
            if (res.data.success === false) throw Error(res.data.error);
            suggestions.value = res.data.suggestions;
        }
    });
}

export default function useState() {
    return {
        loading: computed(() => loading.value),
        error: computed(() => error.value),
        content: computed(() => content.value),
        suggestions: computed(() => suggestions.value),
        resetState,
        getSuggestions,
        searchError,
        addSuggestion,
        newSearch,
        updateContent,
        fetchShow,
        setLoading,
    }
}