<template>
    <section class="results">
        <section v-if='loading' class="loader__container">
            <div class="loader"></div>
        </section>

        <section v-else-if="error" class='error-box'>
            {{ error }}
         </section>
        <div
            v-else
            v-for="result in results"
            :key="result.id"
            @click="onClick(result.id)"
        >
            <img :src="result.image" alt="show image" />
            <div>
                <h2>{{ result.seriesName }}</h2>
                <div v-if="result.firstAired">First Aired: {{ dateFormat(result.firstAired) }}</div>
                <div>Status: {{ result.status }}</div>
            </div>
        </div>
    </section>
</template>

<script lang="ts">
import dayjs from "dayjs";
import { SearchResultModel } from "@/models/Models";
import { useRouter } from "vue-router";
import useState from "@/StateStore";
import { defineComponent, onMounted, ref, watch } from "vue";

export default defineComponent({
    props: {
        query: {
            type: String,
            required: true,
        },
    },
    setup(props) {
        const router = useRouter();
        const { newSearch, loading } = useState();
        const results = ref<Array<SearchResultModel>>([]);
        const error = ref<string|null>(null);

        const onClick = (id: string) =>
            router.push({ name: "show", params: { id } });

        const dateFormat = (date: string): string =>
            dayjs(date).format("MMMM D, YYYY");

        const doSearch = async (value: string) => {
            try {
                results.value = await newSearch(value);
            } catch (e) {
                error.value = e.message;
            }
        }

        onMounted(() => {
            // console.log("Search results mounted");
            doSearch(props.query);
        });

        watch(
            () => props.query,
            (value) => {
                // console.log("Query changed");
                doSearch(value);
            }
        );

        return {
            onClick,
            results,
            dateFormat,
            loading,
            error
        };
    },
});
</script>

<style scoped>
.results {
    margin-bottom: 3.5rem;
    color: white;
    font-weight: bold;
    display: inline-block;
}

.results > div {
    display: flex;
    flex-direction: row;
    margin: 1rem auto 1rem 0;
    cursor: pointer;
    padding-bottom: 0.75rem;
    border-bottom: 1px solid #aeaeae;
    min-width: 30vw;
}

.results > div h2 {
    margin: 0 0 0.75rem;
}

.results > div > img {
    height: 120px;
    display: block;
    margin-right: 1rem;
}

@media screen and (max-width: 575px) {
    .results > div h2 {
        font-size: 1.25rem;
    }
}
</style>
