
import { computed, defineComponent } from 'vue';
import dayjs from 'dayjs';
import {ActorModel, ShowModel} from '../../models/Models';
import ActorsList from '../ActorsList.vue';

export default defineComponent({
    props: {
        content: {
            type: Object as () => ShowModel,
            required: true
        },
    },
    setup(props) {
        const firstAired = computed((): string => {
            return dayjs(props.content.firstAired).format('MMMM D, YYYY');
        });
        const topActors = computed((): Array<ActorModel> => {
            return props.content.actors.filter((actor: ActorModel) => actor.sortOrder <= 3).slice(0, 6);
        });

        const getLink = (item: string) => {
            return `https://www.imdb.com/search/title?genres=${item === "Science-Fiction" ? "Sci-Fi": item}`;
        };

        return {
            firstAired, topActors, getLink
        };
    },
    components: {
        ActorsList
    },
});
