import { defineComponent } from 'vue';
import { ActorModel } from '../models/Models';

export default defineComponent({
    name: 'Actor',
    props: {
        actor: {
            type: Object as () => ActorModel,
            required: true,
        }

    },
    setup() {
        const linko = (name: string) => `http://www.imdb.com/find?s=nm&q=${name}`;

        return {
            linko
        }
    },

    render() {
        return (
            <a href={this.linko(this.actor.name)} target="_blank" class="actor-item">
                {this.actor.image ?
                    <img class="actor-item__image" src={this.actor.image} />
                    :
                    <div class='actor-item__image-missing'><i class="fas fa-user-times"></i></div>
                }
                <section class='actor-item__text'>
                    <div class="actor-item__name">{this.actor.name}</div>
                    <div class="actor-item__role">{this.actor.role}</div>
                </section>
            </a>
        );
    }
});