<template>
  <div class="tab-panel">
    <h3 style="margin-top: 1rem; margin-bottom: 1rem">
      <i class="fas fa-tv"></i>
      {{ seasons.episodes }} Episodes
    </h3>
    <h3>Seasons</h3>
    <div class="btn-group" aria-label="Seasons Button Group">
      <button
        v-for="(season, index) in seasonList"
        @click="bringMeSeason(season)"
        :key="index"
        :class="{ active: season === selectedSeason }"
      >
        {{ season }}
      </button>
    </div>
    <div v-if="selectedSeason && episodes">
      <div v-for="(episode, i) in episodes" :key="i" class="episode">
        <h5>{{ episode.episodeName }} ({{ toDate(episode.firstAired) }})</h5>
        <p>
          {{ episode.overview }}
        </p>
      </div>
    </div>
  </div>
</template>
<script lang='ts'>
import axios from "axios";
import dayjs from "dayjs";
import { baseUrl } from "@/StateStore";
import { EpisodeModel } from "@/models/Models";
import { defineComponent } from "vue";

export default defineComponent({
  props: {
    seasons: Object,
    showId: Number,
  },
  data() {
    return {
      selectedSeason: 0,
      episodes: Array<EpisodeModel>(),
    };
  },
  computed: {
    seasonList() {
      const seasons = [];
      if (this.seasons) {
        for (let i = 0; i < this.seasons.seasons; i++) {
          seasons.push(i + 1);
        }
      }

      return seasons;
    },
  },
  methods: {
    bringMeSeason(n: number) {
      this.selectedSeason = n;
      this.episodes = [];

      axios.get(`${baseUrl}episodes/${this.showId}/${n}`).then((res) => {
        if (res.data.success === false) {
          alert("Sorry, error.");
          return;
        }

        this.episodes = res.data.episodes as EpisodeModel[];
      });
    },
    toDate(date: string) {
      return dayjs(date).format("MMMM D, YYYY");
    },
  },
  mounted() {
    this.selectedSeason = 1;
    this.bringMeSeason(this.selectedSeason);
  },
});
</script>
<style scoped>
.btn-group {
  width: 98%;
  overflow: auto;
  display: flex;
  flex-direction: row;
}

.btn-group > button {
  background-color: #375a7f;
  color: white;
  border: none;
  padding: 1rem;
  border: 1px solid #ffffff66;
  transition: background-color 0.3s linear;
  cursor: pointer;
}

.btn-group > button.active {
  background-color: skyblue;
}

.episode h5 {
  font-size: 1.17rem;
  padding: 1.75rem 0 0.5rem;
  margin: 0;
}

.episode p {
  margin: 0;
  padding: 0;
}
</style>
