<template>
    <section class="topbar">
        <form @submit.prevent="onSubmit" class="search-form">
            <input
                class="form-control"
                type="search"
                name="searcho"
                placeholder="Search the TVDB"
                @focus="elementFocus"
                @blur="elementBlur"
                v-model="value"
                list="datolisto"
            />
            <button type="submit" class="submit-btn" aria-label="Search">
                <i class="fas fa-tv"></i>
            </button>
            <datalist id="datolisto">
                <option v-for="(item, index) in suggestions" :key="index">
                    {{ item }}
                </option>
            </datalist>
        </form>
    </section>
</template>

<style scoped>
.topbar {
    position: sticky;
    top: 0;
    padding: 1rem 0 1rem 3rem;
    background: #222;
    border-bottom: 1px solid #fff;
    background-image: linear-gradient(180deg, #484747 0, #656565 100%);
    opacity: 0.95;
    transition: opacity 0.5s;
    z-index: 5000;
}

.topbar.focus {
    opacity: 1;
}

.submit-btn {
    color: white;
    background-color: skyblue;
    border: none;
    border-radius: 0 6px 6px 0;
    padding: 0 0.5rem;
    font-size: 16px;
    outline: none;
}

@media screen and (min-width: 576px) {
    .search-form {
        max-width: 21rem;
    }
}

@media screen and (max-width: 575px) {
    .topbar {
        padding: 0.75rem 0;
    }

    .search-form {
        justify-content: center;
        margin: 0 1.5rem;
    }
}


.search-form {
    display: flex;
}

.form-control {
    display: block;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 0.75rem;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: #444;
    background-color: #fff;
    background-clip: padding-box;
    border: none;
    border-radius: 6px 0 0 6px;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    outline: none;
}
</style>

<script>
import { onMounted, ref } from "vue";
import useState from "../StateStore";
import { useRouter } from 'vue-router';

export default {
    props: {
        superValue: String,
    },
    setup() {
        const {
            getSuggestions,
            suggestions,
            resetState
        } = useState();

        const router = useRouter();
        const value = ref("");

        onMounted(() => {
            getSuggestions();
        })

        function onSubmit() {
            // console.log("on submit");
            const temp = value.value.trim();
            if (temp.length === 0) {
                resetState();
                router.push({name: 'home'});
            } else {
                value.value = "";
                router.push({name: 'search', params: { query: temp } });
            }
        }

        return {
            suggestions,
            value,
            onSubmit,
        };
    },
    methods: {
        elementFocus() {
            document.querySelector(".topbar").classList.add("focus");
        },
        elementBlur() {
            document.querySelector(".topbar").classList.remove("focus");
        },
    },
};
</script>
